	import { ajax } from '@/static/js/base.js';
	import api from '@/static/js/api.js';	
export default {
	data() {
		return {
			info:{},
		};
	},
	onLoad(e) {
		this.id = e.id
		ajax({
			url: api.getSupplierInfo,
			type: "POST",
			data: {
				id: this.id
			},
		}).then(res => {
			if (res.status_code == "ok") {
				console.log(res)
				this.info = res.data
			}
		})
	},
	methods: {
		open(){
			 this.$refs.popup.open()
		},
		submit() {
			ajax({
				url: api.getSupplierDetails,
				type: "POST",
				data: {
					id: this.id
				},
			}).then(res => {
				if (res.status_code == "ok") {
					console.log(res)
					this.info.mobile = res.data.mobile
					// this.getInfo()
					// this.info = res.data
				}else if(res.status_code == 'error'){
					this.$refs.popup.open();
				}else{
					uni.showToast({
						title:res.message,
						icon:'none'
					})
				}
			})
		},
		close() {
			this.$refs.popup.close()
		},
		navMembership(){
			uni.navigateTo({
				url:'../postSupplier/postSupplier'
			})
			this.$refs.popup.close()
		},
	}
};
